.gnav {
  position: fixed;
  top: 2em;
  right: 2em;
  text-align: left;
  z-index: 1000;
  color: #fff;
}

@media (max-width: 768px) {
  .gnav {
    top: 20px;
    right: 5px;
  }
}

@media (max-width: 321px) {
  .gnav {
    top: 15px;
  }
}

.gnav ul, .gnav li {
  margin: 0;
  padding: 0;
}

.gnav.menu_open ul {
  opacity: 1;
  visibility: visible;
}

.gnav.menu_open #btn_menu {
  background-color: #490000;
  color: #fff;
}

.gnav ul {
  visibility: hidden;
  transition: .2s;
  position: absolute;
  opacity: 0;
  top: 2.5em;
  right: 0;
  width: 250px;
  list-style-type: none;
  border-bottom: none;
}

.gnav ul::before {
  position: absolute;
  top: -15px;
  right: 14px;
  content: "";
  border: 7px solid transparent;
  border-bottom: 7px solid #490000;
}

.gnav ul a {
  display: block;
  background-color: #490000;
  border-bottom: 1px solid rgba(255, 255, 255, 0.2);
  padding: .7em;
  text-decoration: none;
  color: white;
  font-size: 100%;
  transition: .2s;
}

.gnav ul a:hover {
  background-color: rgba(255, 255, 255, 0.9);
  color: #490000;
}

.gnav #btn_menu {
  position: absolute;
  right: 0;
  cursor: pointer;
  padding: 8px 14px;
  transition: .2s;
  background-color: #CB0100;
  border-radius: 3px;
}

.gnav #btn_menu:hover {
  background-color: #490000;
  color: #fff;
}

