.sns-buttons {
  text-align: right;
}

.sns-buttons li {
  display: inline-block;
  margin-right: 8px;
}

.sns-buttons li > a {
  font-size: 24px;
  line-height: 1;
  text-decoration: none;
  color: white;
  padding: 10px 6px 5px 10px;
  border-radius: 100%;
  display: inline-block;
  opacity: 0.8;
}

.sns-buttons li > a:hover {
  opacity: 1;
}

@media (max-width: 768px) {
  .sns-buttons li > a {
    font-size: 16px;
    padding: 7px 6px 5px 7px;
  }
}

.sns-buttons a.share__button--twitter {
  background-color: #55acee;
}

.sns-buttons a.share__button--facebook {
  background-color: #3c5a99;
}

.result {
  border: 1px solid rgba(94, 71, 67, 0.8) !important;
  line-height: 1;
}

.result dl {
  margin: 0 0 0 10px;
}

.result dl dt, .result dl dd {
  padding-top: 0;
  padding-bottom: 0;
}

.result .photo {
  margin-top: 8px;
}
